        <text class="feature-desc">ËàíÁºìÂøÉÁÅµÁöÑÈü≥‰πêÁñóÊÑà</text>
      </view>

      <view class="card feature-card main-card" @tap="navigateToScreening">
        <view class="feature-icon">üß†</view>
        <text class="feature-title">ËΩªÈáèÁ≠õÊü•</text>
        <text class="feature-desc">Âø´ÈÄüËØÑ‰º∞ÂøÉÁêÜÁä∂ÊÄÅ</text>
      </view>

      <view class="card feature-card main-card" @tap="navigateToAI">
        <view class="feature-icon">ü§ñ</view>
