<script>
import { musicAPI } from '@/utils/request.js';
import { isAuthed, getUid, getUserInfo } from '@/utils/auth.js';
import tabBarManager from '@/utils/tabbar-manager.js';

export default {
	data() {
		return {
			recommendList: []
		}
	},
	computed: {
		welcomeText() {
			const hour = new Date().getHours();
			if (hour < 6) return 'Â§úÊ∑±‰∫ÜÔºåÊ≥®ÊÑè‰ºëÊÅØ';
			if (hour < 12) return 'Êó©‰∏äÂ•Ω';
			if (hour < 18) return '‰∏ãÂçàÂ•Ω';
			return 'Êôö‰∏äÂ•Ω';
		},
		isLoggedIn() {
			return isAuthed();
		},
		userGreeting() {
			if (!this.isLoggedIn) return '';
			const userInfo = getUserInfo();
			const name = userInfo?.nickname || 'Áî®Êà∑';
			return `Ê¨¢ËøéÂõûÊù•Ôºå${name}`;
		}
	},
	onLoad() {
		this.loadRecommendMusic();
	},
	onShow() {
		// Âà∑Êñ∞Áî®Êà∑Áä∂ÊÄÅ
		this.$forceUpdate();
		// ÈÄöÁü•ÂØºËà™Ê†èÊõ¥Êñ∞Áä∂ÊÄÅ
		tabBarManager.setCurrentIndexByPath('/pages/home/home');
	},
	methods: {
		// Âä†ËΩΩ‰ªäÊó•Êé®ËçêÈü≥‰πêÔºàÂ∏¶ÈîôËØØÂ§ÑÁêÜÔºâ
		async loadRecommendMusic() {
			try {
				console.log('[HOME] Âä†ËΩΩÊé®ËçêÈü≥‰πê');
				
				// Ëé∑ÂèñÁ¨¨‰∏Ä‰∏™ÂàÜÁ±ª
				const categoriesRes = await musicAPI.getCategories();
				if (categoriesRes.code === 0 && categoriesRes.data.length > 0) {
					const firstCategory = categoriesRes.data[0];
					
					// Ëé∑ÂèñËØ•ÂàÜÁ±ªÁöÑÂâç4È¶ñÈü≥‰πê
					const listRes = await musicAPI.getList(firstCategory, 1, 4);
					if (listRes.code === 0) {
						this.recommendList = listRes.data.list || [];
						console.log('[HOME] Êé®ËçêÈü≥‰πêÂä†ËΩΩÊàêÂäü:', this.recommendList.length);
					} else {
						// APIËøîÂõûÈîôËØØ
						console.warn('[HOME] Êé®ËçêÈü≥‰πêAPIËøîÂõûÈîôËØØ:', listRes.msg);
						this.setFallbackRecommendList();
					}
				} else {
					// ÂàÜÁ±ªÂä†ËΩΩÂ§±Ë¥•
					console.warn('[HOME] Èü≥‰πêÂàÜÁ±ªÂä†ËΩΩÂ§±Ë¥•');
					this.setFallbackRecommendList();
				}
			} catch (error) {
				console.error('[HOME] Âä†ËΩΩÊé®ËçêÈü≥‰πêÂ§±Ë¥•:', error);
				// ‰ΩøÁî®ÈôçÁ∫ßÊï∞ÊçÆ
				this.setFallbackRecommendList();
			}
		},
		
		// ËÆæÁΩÆÈôçÁ∫ßÊé®ËçêÂàóË°®
		setFallbackRecommendList() {
			this.recommendList = [
				{
					_id: 'fallback_1',
					title: 'Ê∑±Â∫¶ÂëºÂê∏ÁªÉ‰π†',
					cover: '/static/images/meditation-cover.png',
					duration: 600,
					locked: false
				},
				{
					_id: 'fallback_2',
					title: 'Ê£ÆÊûóÊº´Ê≠•',
					cover: '/static/images/nature-cover.png',
					duration: 900,
					locked: false
				}
			];
			console.log('[HOME] ‰ΩøÁî®ÈôçÁ∫ßÊé®ËçêÂàóË°®');
		},

		// Ê†ºÂºèÂåñÊó∂Èïø
		formatDuration(seconds) {
			const mins = Math.floor(seconds / 60);
			const secs = seconds % 60;
			return `${mins}:${secs.toString().padStart(2, '0')}`;
		},

		// ÂØºËà™ÊñπÊ≥ï - Ë∑≥ËΩ¨Âà∞featuresÈ°µÈù¢ÁöÑÂØπÂ∫îÊ®°Âùó
		navigateToMusic() {
			uni.switchTab({ 
				url: '/pages/features/features?tab=music' 
			});
		},
		
		navigateToScreening() {
			uni.switchTab({ 
				url: '/pages/features/features?tab=screening' 
			});
		},
		
		navigateToAI() {
			uni.switchTab({ 
				url: '/pages/features/features?tab=ai' 
			});
		},

		// Êí≠ÊîæÈü≥‰πê
		playMusic(item) {
			uni.switchTab({ 
				url: `/pages/features/features?tab=music&id=${item._id}` 
			});
		},

		// Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
		goToAuth() {
			uni.navigateTo({
				url: '/pages/login/login'
			});
		}
	}
}
